.editable-data-panel {
  font-size: 12px;
  margin: 12px 0;
  position: relative;
  top: -15px;
  width: 100%;
  line-height: 12px;
}
.editable-data-panel a {
  background: #000000;
  line-height: 12px;
  color: #ffffff;
  text-decoration: none;
  border: 1px solid #fff;
  padding: 3px 5px;
  margin: 2px;
  display: inline-block;
}
.editable-data-panel input[type=text] {
  width: 100%;
}
.editable-pricing__feature-list-panel {
  margin-bottom: 10px;
  text-align: left;
}
.editable-table_data-panel a {
  position: absolute;
  top: 15px;
  right: 15px;
}
.pricing {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-justify-content: center;
  justify-content: center;
  width: 100%;
  margin: 0 auto 48px;
}
.pricing__item {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-align-items: stretch;
  align-items: stretch;
  text-align: center;
  /*-webkit-flex: 0 1 330px;*/
  -webkit-flex: 0 1 23%;
  flex: 0 1 23%;
}
.pricing__sentence {
  font-size: 16px;
}
.pricing__feature-list {
  font-size: 16px;
}
.pricing__feature-list {
  text-align: left;
}
.pricing__action {
  color: inherit;
  border: none;
  background: none;
}
.pricing__action:focus {
  outline: none;
}
/* Individual styles */
/* Sonam */
.pricing--sonam .pricing__item {
  margin: 16px;
  padding: 32px;
  cursor: default;
  border-radius: 10px;
  background: #1F1F1F;
  box-shadow: 0 5px 20px rgba(0, 0, 0, 0.05), 0 15px 30px -10px rgba(0, 0, 0, 0.3);
  -webkit-transition: background 0.3s;
  transition: background 0.3s;
}
.pricing--sonam .pricing__item:hover {
  background: #141315;
}
.pricing--sonam .pricing__title {
  font-size: 32px;
  color: #ffffff;
  font-weight: bold;
  width: 100%;
  margin: 0 0 4px;
  padding: 0 0 32px;
  border-bottom: 3px solid #1b1a1c;
}
.pricing--sonam .pricing__price {
  color: #E06060;
  font-size: 28px;
  padding: 16px 0 12px;
}
.pricing--sonam .pricing__sentence {
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;
}
.pricing--sonam .pricing__feature-list {
  list-style: disc;
  margin: 0;
  color: #ffffff;
  font-size: 16px;
  padding: 16px 20px 32px;
}
.pricing--sonam .pricing__action {
  text-decoration: none;
  font-weight: bold;
  margin-top: auto;
  padding: 12px 32px;
  border-radius: 5px;
  background: #E06060;
  -webkit-transition: background 0.3s;
  transition: background 0.3s;
  font-size: 16px;
  color: #ffffff;
}
.pricing--sonam .pricing__action:hover,
.pricing--sonam .pricing__action :focus {
  background: #BD3C3C;
}
/* Jinpa */
.pricing--jinpa .pricing__item {
  font-family: 'Sahitya', serif;
  margin: 24px 0;
  padding: 32px;
  cursor: default;
  color: #fff;
  background: #A2E29E;
  border: 1px solid #CBFFC8;
  -webkit-transition: background-color 0.6s, color 0.3s;
  transition: background-color 0.6s, color 0.3s;
}
.pricing--jinpa .pricing__item:nth-child(2) {
  border-right: none;
  border-left: none;
}
.pricing--jinpa .pricing__item:hover {
  color: #444;
  background: #CBFFC8;
}
.pricing--jinpa .pricing__item:first-child .pricing__price {
  background: #eac36e;
}
.pricing--jinpa .pricing__item:nth-child(2) .pricing__price {
  background: #eaa36e;
}
.pricing--jinpa .pricing__item:hover .pricing__price {
  color: #fff;
  background: #82C57E;
}
.pricing--jinpa .pricing__item:hover .pricing__action {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}
.pricing--jinpa .pricing__title {
  font-size: 32px;
  width: 100%;
  margin: 0;
  padding: 0;
}
.pricing--jinpa .pricing__price {
  font-size: 23.2px;
  font-weight: bold;
  line-height: 95px;
  width: 100px;
  height: 100px;
  margin: 18.4px auto 16px;
  border-radius: 50%;
  background: #ea716e;
  -webkit-transition: color 0.3s, background 0.3s;
  transition: color 0.3s, background 0.3s;
}
.pricing--jinpa .pricing__sentence {
  font-weight: bold;
}
.pricing--jinpa .pricing__feature-list {
  margin: 0;
  padding: 16px 16px 32px 16px;
  list-style: none;
  text-align: center;
}
.pricing--jinpa .pricing__feature-list li {
  margin: 20px 0;
}
.pricing--jinpa .pricing__action {
  font-weight: bold;
  margin-top: auto;
  padding: 12px 32px;
  opacity: 0;
  color: #fff;
  background: #82C57E;
  -webkit-transition: -webkit-transform 0.3s, opacity 0.3s;
  transition: transform 0.3s, opacity 0.3s;
  -webkit-transform: translate3d(0, -15px, 0);
  transform: translate3d(0, -15px, 0);
}
.pricing--jinpa .pricing__action:hover,
.pricing--jinpa .pricing__action:focus {
  background: #6EA76B;
}
@media screen and (max-width: 960px) {
  .pricing--jinpa .pricing__item {
    max-width: none;
    width: 90%;
    flex: none;
  }
  .pricing--jinpa .pricing__item:nth-child(2) {
    border: 1px solid #fff;
  }
}
